/* ChoosePage */

button {
  font-size: 2vh;
  font-family: inherit;
  cursor: pointer;
  color: #fff;
  width: 6em;
  height: 3em;
  line-height: 2em;
  position: absolute;
  border-radius: 30px;
  right: 37%;
  bottom: 10%;
  background-color: transparent;
  border: white solid 0.2vh;
  z-index: 1;
}

button:hover {
  animation: ani 8s linear infinite;
  border: none;
}

@keyframes ani {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 400%;
  }
}

.glowButton {
  /* padding:2vh; */
  font-family: Karantina-Bold;
  background: linear-gradient(32deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
}

.glowButton:before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
  background-size: 400%;
  border-radius: 35px;
  transition: 1s;
}

.glowButton:hover::before {
  filter: blur(20px);
}



