
@font-face {
    font-family: Karantina-Light;
    src: url('../assets/font/GveretLevinAlefAlefAlef-Regular.otf');
  }
  
  @font-face {
    font-family: Karantina-Bold;
    src: url('../assets/font/ganclm_bold-webfont.ttf');
  }

.rwd-table {
  margin: 1em 0;
  min-width: 300px;
}
.rwd-table tr {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.rwd-table th {
  /* display: none; */
}
.rwd-table td {
  display: block;
}
.rwd-table td:first-child {
  padding-top: 0.5em;
}
.rwd-table td:last-child {
  padding-bottom: 0.5em;
}
/* .rwd-table td:before {
  content: attr(data-th) ": ";
  font-weight: bold;
  width: 6.5em;
  display: inline-block;
} */
@keyframes highOpacity {
    from {
      opacity: 0%;
    }
  
    to {
      opacity: 100%;
    }
  
  }
@media (min-width: 5vw) {
  .rwd-table td:before {
    display: none;
  }
}
.rwd-table th,
.rwd-table td {
  text-align: center;
}
@media (min-width: 5vw) {
  .rwd-table th,
  .rwd-table td {
    display: table-cell;
    padding: 0.25em 0.5em;
  }
  .rwd-table th:first-child,
  .rwd-table td:first-child {
    padding-left: 0;
  }
  .rwd-table th:last-child,
  .rwd-table td:last-child {
    padding-right: 0;
  }
}

