.rwd-table {
    animation: highOpacity 2s ease-in;
    font-family:Karantina-light;
  background: #a0c9df7e;
  color: #fff;
  border-radius: 0.4em;
  overflow-y: scroll;
  display: block;
  width: 40vw;
  height: 55vh;
  right:8vw;
  position: absolute;
}
.rwd-table tr {
  border-color: #46637f;
  /* width: 20vw; */

  /* height:10vh; */
}
.rwd-table td{
    height: auto;
    overflow-y: scroll;
    color:rgb(12, 12, 56)
}
.rwd-table th,
.rwd-table td {
  margin: 0.5em 1em;
}
@media (min-width: 480px) {
  .rwd-table th,
  .rwd-table td {
    padding: 1em !important;
  }
}
.rwd-table th{
  color: #33A9AC;
  font-size: 4vh;
  height: 5vh;
font-family:Karantina-Bold;
text-align: center;
}
